header {
    width: 100%;
    height: 72px;
    
    position: absolute;

    display: flex;
    justify-content: center;
    align-items: center;

    top: 0;
    left: 0;
}

.nav__width {
    width: 1120px;
    
    display: flex;
    justify-content: space-between;
    align-items: center;

    transition: width 0.5s ease-in-out;
}

.nav__width h1 {
    letter-spacing: 0.1em;
}

.nav__width h1 a {
    text-decoration: none;
    color: var(--black);
}

.nav__menu {
    list-style: none;

    display: flex;
    justify-content: center;
    align-items: center;
}

.nav__menu li {
    font-size: 20px;
    margin-left: 10px;

    color: var(--black);
    text-transform: uppercase;
}

.nav__menu li a {
    text-decoration: none;

    color: var(--black);
}

.nav__icon {
    display: none;
}

.nav__icon i {
    font-size: 20px;

    color: var(--black);
}

@media (max-width: 966px) {
    .nav__width {
        width: 500px;
    }
}

@media (max-width: 768px) {
    .nav__width {
        width: 300px;
    }

    .nav__menu {
        display: none;
    }
    
    .nav__icon {
        display: block;
    }
}